.pages-list {
  font-family: "robotobold";
  margin-bottom: 29px;
  &__item {
    display: inline-block;
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    text-decoration: none;
    color: $white;
  }
}



