.header {
  position: relative;
  background: url("../img/Background-header.jpg") center center / cover
    no-repeat;
  padding-top: 101px;
  padding-bottom: 50px;
  flex: 0 0 auto;
  &__container {
    position: relative;
  }
  &__svg-portfolio {
    position: absolute;
    top: 45%;
    left: 50%;
    width: 100%;
    height: 48%;
    opacity: 0.12;
    fill: $white;
    transform: translate(-50%, -50%);
  }
  &__triangle {
    position: absolute;
    bottom: -77.8px;
    right: 0;
    left: 0;
    fill: $white;
    width: 100%;
  }

  &__socials {
    position: absolute;
    top: 53px;
    left: 3;
  }
  &__avatar {
    width: 400px;
    position: relative;
  }
  &__hamburger-menu {
  }

  &--works {
    margin-bottom: 60px;
  }
}



