.about-me {
  display: flex;
  position: relative;
  padding-bottom: 47px;
  &__description {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 1 50%;
    flex-wrap: wrap;
    margin-right: 60px;
    @include tablet {
      flex: 1 1 100%;
      margin-right: 0;
      align-items: center;
    }
  }
  &__skills {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 1 50%;
    @include tablet {
      flex: 1 1 100%;
      align-items: center;
    }
  }
  &__bg-right {
    fill: $white;
    position: absolute;
    top: -175px;
    right: 0;
    width: 50%;
    height: 1009.986px;
    @include tablet {
      fill: $light-grey;
    }
  }
  &__bg-left {
    fill: $light-grey;
    position: absolute;
    top: -178px;
    left: 0;
    width: 50%;
    height: 1009.986px;
  }
  &__title-about {
    height: 100%;
    width: 50%;
    margin-bottom: 0;
    position: relative;
    z-index: 1;
    &:after {
      bottom: 87%;
      @include tablet {
        bottom: 1%;
      }
    }
  }
  &__svg-about {
    position: absolute;
    top: 4%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    fill: $sick-green;
    z-index: -1;
    opacity: 0.1;
    @include tablet {
      top: 50%;
    }
  }
  &__pic {
    width: 50%;
    margin-bottom: 40px;
  }
  &__img {
    width: 100%;
    max-width: 100%;
  }
  &__title-whoIsI {
    width: 50%;
    font-size: 35px;
    margin-bottom: 61px;
    &:after {
      bottom: -80%;
    }
  }
  &__text {
    width: 50%;
    p {
      margin-bottom: 20px;
    }
  }

  &__title-myBenefit {
    font-size: 35px;
    margin-bottom: 35px;
    &:after {
      bottom: -80%;
    }
  }

  &__skills-text {
    margin-bottom: 36px;
  }
  &__skills-list {
    @include tablet {
      width: 100%;
      text-align: center;
    }
  }
  &__skills-item {
    margin-bottom: 18px;
  }

  &__title-skills {
    font-size: 28px;
    font-family: "robotomedium";
    color: $dark-sage;
    margin-bottom: 10px;
  }
  &__circle-list {
    display: flex;
    flex-wrap: wrap;
    @include tablet {
      justify-content: center;
    }
  }

  &__circle-item {
    flex: 0 0 120px;
    height: 120px;
    background: green;
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
  @include tablet {
    flex-direction: column;
  }
}



