.welcome {
  padding-top: 40px;
  margin: 0 auto;
  color: $white;
  text-align: center;
  width: 380px;
  background-color: rgba(#122620, 0.6);
  border-radius: 10px;
  overflow: hidden;
  z-index: 1;

  &__card {
  }
  &__top {
    margin-bottom: 32px;
    @include mobile {
      margin-bottom: 15px;
    }
  }
  &__bottom {
  }
  &__pic {
    width: 141px;
    height: 141px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
  }
  &__img {
    width: 100%;
    max-width: 100%;
    height: 100%;
  }
  &__name-text {
    position: relative;
    font-size: 28px;
    font-family: "sansus_webissimoregular";
    margin-bottom: 13px;
  }
  &__text-amplua {
    margin-bottom: 35px;
    @include mobile {
      margin-bottom: 25px;
    }
  }
  &__socials {
    margin-bottom: 40px;
    @include mobile {
      margin-bottom: 25px;
    }
  }
  &__button-row {
    display: flex;
  }
  &__works {
    flex: 1 1 auto;
  }
  &__about {
    flex: 1 1 auto;
  }
  &__blog {
    flex: 1 1 auto;
  }
  @include mobile {
    padding-top: 25px;
  }
}



